@keyframes scale {
  0% {
    transform: scale(1.1);
    filter: blur(40px);
  }
  100% {
    transform: scale(1);
    filter: blur(0);
  }
}

.swiper-hero {
  display: block;
  z-index: 120;
  width: 100%;
  overflow: visible;
  margin-top: 73px;
  @include screen(laptop) {
    display: none;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    height: auto;
  }
  .hero__el {
    height: 100%;
    justify-content: space-between;
  }
  .swiper-pagination {
    position: static;
    width: auto;
  }
  .swiper-pagination-bullet {
    width: 6px;
    height: 6px;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    opacity: 1;
    margin: 0 5px;
    &-active {
      background-color: $white;
    }
  }
  .swiper-button {
    position: relative;
    &-prev,
    &-next {
      width: 40px;
      border-radius: 50%;
      background-color: rgba(84, 112, 137, 0.15);
      height: 40px;
      backdrop-filter: blur(20px);
      display: flex;
      align-items: center;
      cursor: pointer;
      border: 1px solid rgba(255, 255, 255, 0.08);
      justify-content: center;
      span {
        background-repeat: no-repeat;
        background-position: center;
        width: 12px;
        transition: $transition-default;
        height: 10px;
        opacity: 1;
      }
      &:after,
      &:before {
        display: none;
      }
    }
    &-prev {
      span {
        background-image: url(data:image/svg+xml;base64,);
      }
    }
    &-next {
      span {
        background-image: url(data:image/svg+xml;base64,);
      }
    }
    &-disabled {
      span {
        opacity: 0.2;
      }
    }
  }
}

.hero {
  position: relative;
  padding-top: 90px;
  overflow: hidden;
  @include screen(laptop) {
    padding-top: 100px;
    min-height: 100dvh;
  }
  &__navigation {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-top: 15px;
    @include screen(laptop) {
      display: none;
    }
  }
  &__tip {
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 50%;
    width: 3vw;
    height: 3vw;
    position: absolute;
    z-index: 3;
    min-width: 20px;
    min-height: 20px;
    display: flex;
    align-items: center;
    background-color: rgba(#547089, 0.15);
    backdrop-filter: blur(20px);
    justify-content: center;
    &.active {
      &:before {
        width: 10px;
        height: 10px;
        @include screen(laptop) {
          width: 0.8vw;
          height: 0.8vw;
        }
      }
    }
    &:before {
      content: "";
      transition: $transition-default;
      position: absolute;
      width: 4px;
      height: 4px;
      background-color: $white;
      border-radius: 50%;
      @include screen(laptop) {
        width: 0.8vw;
        height: 0.8vw;
      }
    }
    &:after {
      content: "";
      background-image: url(data:image/svg+xml;base64,);
      position: absolute;
      width: 186px;
      display: none;
      height: 71px;
      @include screen(desktop) {
        display: block;
      }
    }
    &-first {
      top: 63%;
      left: 9%;
      @include screen(laptop) {
        top: 62%;
        left: 8%;
        &:after {
          top: 40px;
          right: 40px;
        }
      }
      @include screen(desktop) {
        .hero__el {
          right: 226px;
        }
      }
    }
    &-second {
      top: 79%;
      left: 25%;
      @include screen(laptop) {
        top: 78%;
        left: 24%;
        &:after {
          top: 40px;
          left: 40px;
          transform: scale(-1, 1);
        }
      }
      @include screen(desktop) {
        .hero__el {
          left: 226px;
        }
      }
    }
    &-third {
      top: 72%;
      right: 2%;
      @include screen(laptop) {
        top: 70%;
        right: 1%;
        &:after {
          top: 40px;
          left: 40px;
          transform: scale(-1, 1);
        }
      }
      @include screen(desktop) {
        .hero__el {
          left: 226px;
        }
      }
    }
  }
  &__bg {
    animation: scale 0.5s ease-in-out forwards 400ms;
    position: absolute;
    overflow: hidden;
    inset: 0;
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      object-fit: cover;
      min-height: 600px;
      object-position: top;
      min-width: 667px;
      @include screen(laptop) {
        object-position: center;
        max-height: 100%;
        height: 100%;
      }
    }
  }
  &__image {
    position: absolute;
    top: 70px;
    z-index: 110;
    left: 49%;
    width: 100%;
    transform: translateX(-50%);
    max-width: 921px;
    @include screen(laptop) {
      top: 0;
      max-width: 50vw;
    }
    img {
      width: 100%;
    }
    .hero__el {
      display: none;
      @include screen(desktop) {
        display: flex;
      }
    }
  }
  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 6px;
    @include screen(laptop) {
      padding-bottom: 20px;
      min-height: calc(100dvh - 100px);
    }
  }
  &__title {
    font-weight: 600;
    font-size: 21vw;
    line-height: 20vw;
    letter-spacing: -2.14vw;
    text-align: center;
    max-height: 38vw;
    z-index: 2;
    margin-bottom: 55vw;
    br {
      display: block;
      @include screen(laptop) {
        display: none;
      }
    }
    @include screen(laptop) {
      margin-bottom: 0;
      max-height: 17vw;
      margin-left: -2vw;
    }
    span {
      text-transform: uppercase;
      background: linear-gradient(180deg, #dbdddd 15.37%, #505e79 73.15%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      display: block;
      user-select: none;
    }
  }
  &__content {
    z-index: 120;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 30px;
    margin-top: 50px;
    @include screen(laptop) {
      margin-top: 30px;
      flex: 1;
    }
  }
  &__el {
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 5px;
    background-color: rgba(84, 112, 137, 0.15);
    padding: 20px;
    display: flex;
    flex-direction: column;
    z-index: 3;
    top: 0;
    align-items: flex-start;
    @include screen(desktop) {
      min-width: 300px;
      position: absolute;
    }
    &:before {
      content: "";
      position: absolute;
      border-radius: 5px;
      inset: 0;
      z-index: -1;
      backdrop-filter: blur(20px);
    }
    h3 {
      font-weight: 500;
      color: $white;
      font-size: 20px;
      line-height: 100%;
      text-transform: uppercase;
      @include screen(desktop) {
        font-size: 26px;
      }
    }
    p {
      margin-top: 10px;
      font-weight: 400;
      font-size: 14px;
      line-height: 120%;
      max-width: 260px;
      color: #80858d;
      @include screen(desktop) {
        font-size: 15px;
      }
    }
    img {
      margin-top: 52px;
      max-width: 124px;
      @include screen(desktop) {
        margin-top: 60px;
      }
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    gap: 10px;
    @include screen(laptop) {
      flex-direction: row;
      gap: 20px;
    }
    &-top {
      gap: 40px;
      @include screen(laptop) {
        gap: 10px;
      }
    }
    &-bottom {
      padding-top: 20px;
      padding-bottom: 20px;
      position: relative;
      @include screen(desktop) {
        margin-top: 538px;
        padding-top: 0;
      }
      &:after,
      &:before {
        content: "";
        position: absolute;
        width: 100%;
        bottom: 0;
        border-bottom: 1px solid;
        border-image-slice: 1;
        border-image-source: linear-gradient(
          to right,
          transparent 0%,
          rgba(255, 255, 255, 0.2) 20%,
          rgba(255, 255, 255, 0.2) 80%,
          transparent 100%
        );
      }
      &:after {
        bottom: auto;
        top: 0;
        display: block;
        @include screen(laptop) {
          display: none;
        }
      }
    }
  }
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    min-width: 148px;
    gap: 5px;
    @include screen(laptop) {
      gap: 10px;
      min-width: 246px;
    }
  }
  &__item {
    gap: 10px;
    display: flex;
    flex-direction: column;
    &-right {
      h3,
      p {
        @include screen(laptop) {
          text-align: right;
        }
      }
      p {
        @include screen(laptop) {
          padding-right: 27px;
          padding-left: 0;
          background-position: right;
        }
      }
    }
    h3 {
      color: $white;
      font-weight: 300;
      line-height: 100%;
      font-size: 18px;
      text-transform: uppercase;
      @include screen(laptop) {
        font-size: 22px;
      }
      br {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
    }
    p {
      text-transform: uppercase;
      font-weight: 300;
      font-size: 14px;
      color: #80858d;
      line-height: 100%;
      letter-spacing: -0.3px;
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      padding-left: 27px;
    }
    &-lg {
      max-width: 480px;
      h3 {
        font-size: 26px;
        letter-spacing: -1px;
        line-height: 1;
        @include screen(laptop) {
          letter-spacing: -3px;
          font-size: 48px;
        }
        span {
          font-weight: 500;
        }
      }
      p {
        height: 18px;
        display: flex;
        align-items: center;
        padding-top: 2px;
        font-size: 16px;
        background-image: url(data:image/svg+xml;base64,);
        @include screen(laptop) {
          font-size: 14px;
          background-image: url(data:image/svg+xml;base64,);
        }
      }
    }
    &-block {
      border: 1px solid rgba(255, 255, 255, 0.08);
      border-radius: 5px;
      padding: 15px 10px;
      backdrop-filter: blur(20px);
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: row;
      gap: 10px;
      @include screen(laptop) {
        padding: 20px;
        gap: 60px;
      }
      h3 {
        font-size: 22px;
        @include screen(laptop) {
          font-size: 26px;
        }
      }
      p {
        padding-left: 0;
        background: none;
        text-transform: unset;
        max-width: 140px;
        font-size: 15px;
        @include screen(laptop) {
          text-transform: uppercase;
          max-width: 100%;
          font-size: 14px;
        }
      }
      .counter {
        font-size: 40px;
        padding-right: 20px;
        font-weight: 300;
        display: flex;
        align-items: center;
        line-height: 100%;
        margin-bottom: -10px;
        color: $white;
        background-image: url(data:image/svg+xml;base64,);
        background-repeat: no-repeat;
        background-position: right center;
        background-size: 16px 9.5px;
        span {
          min-width: 59px;
          display: inline-block;
          @include screen(laptop) {
            min-width: 118px;
          }
        }
        @include screen(laptop) {
          font-size: 80px;
          padding-right: 33px;
        }
      }
    }
  }
}