* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

textarea,
input,
button,
select,
a {
  outline: none;
  border-radius: 0;

  font-weight: 400;
  font-family: "Mohave", ui-sans-serif, system-ui, -apple-system,
    BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans",
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol",
    "Noto Color Emoji";
  touch-action: manipulation;
}

html,
body {
  height: 100%;
  background-color: $black;
  font-family: "Mohave", ui-sans-serif, system-ui, -apple-system,
    BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans",
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol",
    "Noto Color Emoji";
}

body {
  &.active {
    &:after {
      content: "";
      position: fixed;
      background-color: rgba(#06101e, 0.95);
      inset: 0;
      z-index: 120;
      height: 100dvh;
    }
  }
}

.blur {
  pointer-events: none;
  filter: blur(10px);
}

.render {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;

  width: 100%;
  height: 100%;

  background-color: $black;
}

.wrapper {
  margin: 0 auto;
  padding: 0 10px;
  width: 100%;
  @include screen(laptop) {
    padding: 0 20px;
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}

.reveal-from-top,
.reveal-from-bottom,
.reveal-from-opacity,
.reveal-from-opacity-delay,
.reveal-from-opacity-2x {
  @include screen(laptop) {
    visibility: hidden;
  }
}