.header {
  display: flex;
  align-items: center;
  position: fixed;
  width: 100%;
  z-index: 130;
  &__wrapper {
    padding: 0 10px;
    height: 60px;
    display: flex;
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 5px;
    background-color: rgba(84, 112, 137, 0.15);
    align-items: center;
    justify-content: space-between;
    z-index: 2;
    top: 10px;
    position: relative;
    &:before {
      content: "";
      position: absolute;
      border-radius: 5px;
      inset: 0;
      backdrop-filter: blur(20px);
    }
    @include screen(laptop) {
      display: grid;
      padding: 0 20px;
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
  }
  &__logo {
    z-index: 2;
    grid-column: span 1 / span 1;
    background-repeat: no-repeat;
    color: $white;
    padding-left: 25px;
    background-position: left 3px center;
    background-image: url(data:image/svg+xml;base64,);
    p {
      font-size: 16px;
      line-height: 100%;
    }
    span {
      vertical-align: top;
      margin-left: 2px;
      font-size: 8px;
      line-height: 100%;
    }
  }
  &__link {
    color: #80858d;
    font-weight: 300;
    display: flex;
    align-items: center;
    font-size: 14px;
    border-bottom: 2px solid transparent;
    line-height: 100%;
    height: 100%;
    &.active {
      border-color: #2461fe;
      color: $white;
    }
    @include screen(laptop) {
      transition: $transition-default;
      &:hover {
        border-color: #2461fe;
        color: $white;
      }
    }
  }
  &__menu {
    align-items: center;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    display: none;
    z-index: 2;
    padding-top: 10px;
    padding-bottom: 20px;
    grid-column: span 2 / span 2;
    gap: 15px;
    top: 75px;
    overflow: hidden;
    &:before {
      content: "";
      position: absolute;
      inset: 0;
      border-radius: 5px;
      z-index: -1;
      backdrop-filter: blur(20px);
      @include screen(laptop) {
        display: none;
      }
    }
    @include screen(laptop) {
      display: grid;
      gap: 0;
      width: auto;
      height: 100%;
      padding-top: 0;
      padding-bottom: 0;
      &:before {
        display: none;
      }
    }
    &.active {
      display: flex;
      position: fixed;
      right: 10px;
      left: 10px;
      flex-direction: column;
      align-items: flex-start;
      border: 1px solid rgba(255, 255, 255, 0.08);
      border-radius: 5px;
      z-index: 100;
      background-color: rgba(84, 112, 137, 0.15);
      .header__nav {
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        gap: 5px;
      }
      .header__buttons {
        align-items: flex-start;
        flex-direction: column-reverse;
        padding: 0 20px;
        width: 100%;
      }
      .header__button {
        justify-content: center;
        width: 100%;
        font-size: 14px;
      }
      .header__link {
        border-left: 2px solid transparent;
        border-bottom: none;
        padding: 5px 20px;
        font-size: 16px;
        &.active {
          border-color: #2461fe;
          color: $white;
        }
      }
    }
  }
  &__nav {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    gap: 40px;
  }
  &__buttons {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 10px;
  }
  &__button {
    padding: 6px 10px;
    display: flex;
    align-items: center;
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 5px;
    color: $white;
    @include screen(laptop) {
      transition: $transition-default;
      &:hover {
        background-color: rgba(255, 255, 255, 0.1);
      }
    }
    &-icon {
      background-repeat: no-repeat;
      background-position: calc(50% + 36px) center;
      background-image: url(data:image/svg+xml;base64,);
      @include screen(laptop) {
        background-position: right 10px center;
      }
    }
    &-white {
      background-color: $white;
      padding: 6px 28px 6px 10px;
      color: $black;
      font-weight: 500;
      @include screen(laptop) {
        transition: $transition-default;
        &:hover {
          background-color: rgba(255, 255, 255, 0.7);
        }
      }
    }
  }
  &__burger {
    height: 32px;
    width: 32px;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include screen(laptop) {
      display: none;
    }
    svg {
      height: 24px;
      rect {
        transition: $transition-default;
      }
    }
    &.active {
      svg {
        rect {
          &:nth-child(1) {
            transform: rotate(45deg) translate(-1px, -7px);
          }
          &:nth-child(2) {
            width: 24px;
            transform: rotate(-45deg) translate(-16px, 5px);
          }
        }
      }
    }
  }
}